/* BUTTONS ================================================ BUTTONS */

.button {
    margin: 1em;
    width: 10em;
    height: 4em;
    background-color: transparent;
    border: 1.5px solid #000;
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
} .button:focus {
    outline: none;
} .button:active {
    background-color: #eee;
}

.solid[class~="button"] {
    color: #fff;
    background-color: #000;
    border: 1.5px solid #000;
} .solid[class~="button"]:active {
    background-color: #1d1d1d;
    border: 1.5px solid #1d1d1d;
}

/* GREEN Buttons */
.green[class~="solid"] {
    color: #fff;
    background-color: #177400;
    border: 1.5px solid #177400;
} .green[class~="solid"]:active{
    background-color: #125a00;
    border: 1.5px solid #125a00;
} .green:not([class~="solid"]) {
    color: #177400;
    background-color: transparent;
    border: 1.5px solid #177400;
} .green:not([class~="solid"]):active {
    background-color: #eeffee;
}

/* RED Buttons */
.red[class~="solid"] {
    color: #fff;
    background-color: #bd0000;
    border: 1.5px solid #bd0000;
} .red[class~="solid"]:active{
    background-color: #990101;
    border: 1.5px solid #990101;
} .red:not([class~="solid"]) {
    color: #bd0000;
    background-color: transparent;
    border: 1.5px solid #bd0000;
} .red:not([class~="solid"]):active {
    background-color: #ffeeee;
}

/* DISABLED Buttons */
.button:disabled {
    color: #fff;
    background-color: #ddd;
    border: 1.5px solid #ddd;
}

.small[class~="button"] {
    margin: 0.5em;
    width: 9em;
    height: 3em;
    font-size: 0.65em;
}

/* INPUTS ============================================= INPUTS */
.textbox {
    margin: 1em;
    width: 20em;
    padding: 1em;
    font-size: 0.85em;
    background-color: #fff;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
    border: 1.5px solid #000;
} .textbox:focus{
    outline: none;
    background-color: #eee;
} .textbox[class~="error"] {
    color: #d60000;
    border: 1.5px solid #d60000;
} .textbox[class~="error"]::-webkit-input-placeholder {
    color: #d60000;
} .textbox[class~="error"]::-ms-input-placeholder {
    color: #d60000;
} .textbox[class~="error"]::placeholder {
    color: #d60000;
}

