/* FEATURES ============================================= FEATURES */

/* ROTARY */
/* Useage: Insert a div with "rotary" class inside a container*/
/* change width-height of container to control size */
.rotary {
    margin: auto auto;
    width: 100%;
    height: 100%;
    position: relative;
} .rotary:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    right: 0;
    left: 0;
    z-index: -1;
    background-color: #ffee00;
    background-color: var(--yellow);
} .rotary:hover::after {
    -webkit-animation: rotor 1000ms ease 5;
            animation: rotor 1000ms ease 5;
}
@-webkit-keyframes rotor {
    0%{
        width: 100%;
        right: 0;
        left: 0;
    }
    49%{
        width: 0;
        right: 0;
        left: 100%;
    }
    50%{        
        width: 0;
        left: 0;
        right: 100%;
    }
    99%{
        width: 100%;
        left: 0;
        right: 0;
    }
    100% {
        width: 100%;
        right: 0;
        left: 0;
    }
}
@keyframes rotor {
    0%{
        width: 100%;
        right: 0;
        left: 0;
    }
    49%{
        width: 0;
        right: 0;
        left: 100%;
    }
    50%{        
        width: 0;
        left: 0;
        right: 100%;
    }
    99%{
        width: 100%;
        left: 0;
        right: 0;
    }
    100% {
        width: 100%;
        right: 0;
        left: 0;
    }
}
